* {
  margin: 0;
  padding: 0;
}
.clear {
  clear: both;
}
.movieBox {
  margin: 5px;
  padding: 5px;
}
.movieBox div {
  margin: 10px 0;
}
.inline {
  display: inline;
  float: left;
}
.floatUl {
  display: inline;
  list-style: none;
}
.li {
  float: left;
  margin: 0 5px;
  font-weight: bold;
}
.li::after {
  content: ",";
}
.li:last-child::after {
  content: "";
}
.defaultBtn {
  padding: 10px;
  color: #eaeaea;
  background-color: fff;
  border: none;
  text-decoration: none;
}
