.movieLayer{width: 30%; height: 350px; float: left; margin: 30px; background: #f8f8f1;}
.movieLayer::before{content:""; position: absolute; height: 50%;width: 50%;background: linear-gradient(212deg, #ECE9E6, #777); filter: blur(30px); z-index: -1;}
.movieLayer h2{background: #3F0099; padding: 5px; height: 32px; overflow: hidden;}
.movieLayer h2 a{width: 100%; color:#fff; overflow: hidden; text-decoration: none; }
.movieLayer .leftArea, .movieLayer .rightArea{float: left; height: calc(100% - 42px);}
.movieLayer .leftArea{width: 30%;}
.movieLayer .rightArea{width: 68%; padding: 5px;}
.movieLayer .rightArea p{margin: 8px 0; color: #3F0099; font-weight: 700;}
.movieLayer .rightArea p::before{content: " - ";}
.movieLayer .rightArea p.rating{color: #DD4606;}
.movieLayer .leftArea img, .movieLayer .rightArea img{width: 100%;}
.movieLayer .posterBox{position: relative; top: 55%; left: 40%; transform: translate(-55%, -55%); padding: 5px; width: 90%; height: auto; background-color: #DD4606;}
.movieLayer .summary{color: #000;}
.movieLayer .summary::before{content: "\"";}
.movieLayer .summary::after{content: "\"";}